import React, {
  useEffect,
  useMemo,
  useState,
  useRef,
  useCallback,
} from "react";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import DragAndDropComponent from "../../components/DragAndDrop/DragAndDrop";
import CardWithDragAndDrop from "../../components/Card/cardwithdraganddrop";
import { Image } from "react-bootstrap";
import useDroppableArea from "../../hooks/useDroppableArea";
import Loader from "../../components/Loader/Loader";
import ReusableTable from "../../components/Table/Table";
import {
  setDroppedObjectData,
  setIsDropped,
} from "../../store/droppedObjectSlice";
import { getCardData, getTableData, tableColumns } from "./dataHelpers";
import RevisionFloatToolbarNativeCta from "./revisionFloatToolbarNativeCta";

const RevisionFloat = () => {
  const { initializeDroppableArea, loading } = useDroppableArea();
  const tableKey = useRef(0); // ‚úÖ Use useRef instead of useState
  const dispatch = useDispatch();

  console.log("[RevisionFloat] üéØ Component Rendered");
  // ‚úÖ Use shallowEqual to prevent unnecessary re-renders
  const {
    cardData,
    parentDetails,
    isDropped,
    loadingParentDetails,
    initialDraggedData,
  } = useSelector(
    (state) => ({
      cardData: state.droppedObject.droppedObjectData.cardData,
      parentDetails: state.droppedObject.droppedObjectData.parentDetails,
      isDropped: state.droppedObject.isDropped,
      initialDraggedData:
        state.droppedObject.droppedObjectData.initialDraggedData,
      loadingParentDetails: state.droppedObject.loadingParentDetails,
    }),
    shallowEqual // ‚úÖ Ensures Redux only re-renders when values actually change
  );
  console.log("[RevisionFloat] üè™ Redux State - cardData:", cardData);
  console.log("[RevisionFloat] üè™ Redux State - parentDetails:", parentDetails);

  // ‚úÖ Ensure droppable area is only initialized **once**
  const hasInitializedDroppable = useRef(false);
  useEffect(() => {
    if (!isDropped && !hasInitializedDroppable.current) {
      console.log("[RevisionFloat] üîÑ Initializing droppable area...");
      initializeDroppableArea();
      hasInitializedDroppable.current = true; // ‚úÖ Ensure it runs only once
    }
  }, [isDropped, initializeDroppableArea]);

  // ‚úÖ Optimize `isTableLoading` update (only update if necessary)
  useEffect(() => {
    console.log(
      "[RevisionFloat] üîÑ Updating isTableLoading:",
      loadingParentDetails
    );
  }, [loadingParentDetails]);

  // ‚úÖ Memoize tableData (Only re-run if parentDetails change)
  const optimizedTableData = useMemo(
    () => getTableData(parentDetails, cardData),
    [parentDetails, cardData]
  );

  // ‚úÖ Prevent unnecessary re-renders by checking for data changes
  useEffect(() => {
    if (optimizedTableData.length > 0) {
      console.log(
        "[RevisionFloat] üìä optimizedTableData Updated:",
        optimizedTableData
      );
    }
  }, [optimizedTableData]);

  // ‚úÖ Memoize cardData (Avoid recalculations unless cardData changes)
  const optimizedCardData = useMemo(() => getCardData(cardData), [cardData]);

  // ‚úÖ Reset state only when needed
  const handleHomeClick = useCallback(() => {
    console.log(
      "[handleHomeClick] üè† Home icon clicked. Resetting droppable area..."
    );

    if (!isDropped) {
      console.log(
        "[handleHomeClick] üö´ Already reset. Skipping unnecessary updates."
      );
      return;
    }

    // ‚úÖ Only reinitialize if necessary
    if (!initialDraggedData.length) {
      console.log("[handleHomeClick] üîÑ Reinitializing droppable area...");
      initializeDroppableArea();
    }

    // ‚úÖ Reset Redux state only if needed
    dispatch(setIsDropped(false));

    if (cardData || parentDetails.length || initialDraggedData.length) {
      console.log("[handleHomeClick] üóë Dispatching reset action...");
      dispatch(
        setDroppedObjectData({
          cardData: {},
          parentDetails: [],
          initialDraggedData: [],
        })
      );
    } else {
      console.log(
        "[handleHomeClick] üö´ State already cleared. Skipping dispatch."
      );
    }

    console.log("[handleHomeClick] ‚úÖ Reset complete.");
  }, [
    dispatch,
    initializeDroppableArea,
    isDropped,
    initialDraggedData,
    cardData,
    parentDetails,
  ]);

  // ‚úÖ Memoize filtered columns
  const filteredColumns = useMemo(() => {
    if (cardData?.Type === "Document") {
      return tableColumns;
    } else {
      return tableColumns.filter(
        (column) => column.accessorKey !== "relationship"
      );
    }
  }, [cardData]);
  console.log("[RevisionFloat] ‚úÖ Component Updated");

  return (
    <>
      {!isDropped && !loading && !loadingParentDetails && (
        <DragAndDropComponent />
      )}
      {loading && <Loader />}
      {isDropped && (
        <>
          <div className="content-wrapper py-3 border-bottom">
            <div className="d-flex">
              <div className="p-0 pt-4">
                <Image
                  src="https://thewhitechamaleon.github.io/testrapp/images/home.png"
                  alt="home-icon"
                  className="home-icon"
                  onClick={handleHomeClick}
                />
              </div>
              {optimizedCardData && (
                <CardWithDragAndDrop data={optimizedCardData} />
              )}
            </div>
          </div>

          {loadingParentDetails ? (
            <div className="loading-indicator mt-5">
              <Loader />
            </div>
          ) : optimizedTableData.length > 0 ? (
            <div className="wrapper-cta">
              <RevisionFloatToolbarNativeCta />
              <ReusableTable
                key={tableKey.current}
                data={optimizedTableData}
                columns={filteredColumns}
                widgetType="Revision_FLoat_Widget"
              />
            </div>
          ) : (
            <div className="no-data-banner">No Parent object(s) found</div>
          )}
        </>
      )}
    </>
  );
};

export default RevisionFloat;
